"use client";

import { useEffect, useState } from "react";
import { Bookmark, BookmarkCheck } from "lucide-react";

interface NewsItem {
  title: string;
  link: string;
  contentSnippet?: string;
  pubDate?: string;
}

export default function GovtUpdates() {
  const [news, setNews] = useState<NewsItem[]>([]);
  const [bookmarks, setBookmarks] = useState<string[]>([]);
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchRSS = async () => {
      try {
        const CORS_PROXY = "https://api.allorigins.win/get?url=";
        const RSS_URL = encodeURIComponent(
          "https://pib.gov.in/PressReleaseRssFeed.aspx"
        );

        const res = await fetch(`${CORS_PROXY}${RSS_URL}`);
        const data = await res.json();

        if (!data.contents) {
          console.error("No feed data found");
          setLoading(false);
          return;
        }

        // Parse XML string
        const parser = new DOMParser();
        const xml = parser.parseFromString(data.contents, "application/xml");

        const items = Array.from(xml.querySelectorAll("item")).map((item) => ({
          title: item.querySelector("title")?.textContent || "Untitled",
          link: item.querySelector("link")?.textContent || "#",
          contentSnippet:
            item.querySelector("description")?.textContent || "No description",
          pubDate: item.querySelector("pubDate")?.textContent || "",
        }));

        setNews(items);
      } catch (err) {
        console.error("Failed to fetch RSS feed:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchRSS();
  }, []);

  const toggleBookmark = (link: string) => {
    setBookmarks((prev) =>
      prev.includes(link) ? prev.filter((l) => l !== link) : [...prev, link]
    );
  };

  const filteredNews = news.filter((item) =>
    item.title.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-6">Government Updates 📰</h1>

      <input
        type="text"
        placeholder="Search news..."
        value={search}
        onChange={(e) => setSearch(e.target.value)}
        className="border p-2 rounded-md w-full mb-6"
      />

      {loading ? (
        <p className="text-gray-500">Loading latest government updates...</p>
      ) : filteredNews.length === 0 ? (
        <p className="text-gray-500">No news found.</p>
      ) : (
        <div className="space-y-4">
          {filteredNews.map((item) => (
            <div
              key={item.link}
              className="p-4 border rounded-lg bg-white flex justify-between items-start"
            >
              <div>
                <a
                  href={item.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="font-semibold text-indigo-600 hover:underline text-lg"
                >
                  {item.title}
                </a>
                {item.contentSnippet && (
                  <p className="text-gray-600 text-sm mt-1">
                    {item.contentSnippet}
                  </p>
                )}
                {item.pubDate && (
                  <p className="text-gray-400 text-xs mt-1">{item.pubDate}</p>
                )}
              </div>

              <button onClick={() => toggleBookmark(item.link)}>
                {bookmarks.includes(item.link) ? (
                  <BookmarkCheck className="text-indigo-600 w-6 h-6" />
                ) : (
                  <Bookmark className="text-gray-500 w-6 h-6" />
                )}
              </button>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
